.product-display {
  display: flex;
  margin: 0px 170px;
  margin-bottom: 100px;
}

.product-display-left {
  display: flex;
  gap: 17px;
}

.product-display-img-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.product-display-img-list img {
  height: 130px;
}

.product-display-main-img {
  width: 475px;
  height: auto;
}

.product-display-right {
  margin: 0px 70px;
  display: flex;
  flex-direction: column;
}

.product-display-right h1 {
  color: #3d3d3d;
  font-size: 25px;
  font-weight: 700;
}

.product-display-right-star {
  display: flex;
  align-items: center;
  margin-top: 13px;
  gap: 5px;
  font-size: 16px;
  color: #5c5c5c;
}

.product-display-right-prices {
  display: flex;
  margin: 20px 0px;
  gap: 10px;
  font-size: 24px;
  font-weight: 700;
}
.product-display-right-price-old {
  color: #818181;
  text-decoration: line-through;
}

.product-display-right-price-new {
  color: #ff4141;
}

.product-display-right-size h2 {
  margin-top: 30px;
  color: #656565;
  font-size: 20px;
  font-weight: 600;
}

.product-display-right-sizes {
  display: flex;
  margin: 20px 0px;
  gap: 20px;
}

.product-display-right-sizes div {
  padding: 12px 20px;
  background: #fbfbfb;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  cursor: pointer;
}

.product-display-right button {
  padding: 16px;
  width: 200px;
  font-size: 14px;
  font-weight: 600;
  color: white;
  background: #ff4141;
  margin-bottom: 35px;
  border: none;
  outline: none;
  cursor: pointer;
}

.product-display-right-category {
  margin-top: 10px;
}

.product-display-right-category span {
  font-weight: 600;
}
